@media (max-width:550px) {
    body {
        font-size: 6px;
    }
    .navigation-bar {
        flex-direction: column;
        align-items: flex-end;
    }
    .search-text {
        font-size: 1.2rem;
        margin: 1rem 0 1rem 0;
        letter-spacing: 0;
    }
    .search-form  {
        flex-direction: column;
    }
    .compare-text {
        font-size: 1rem;
    }
    .features--title {
        font-size: 2rem;
    }
    .features--main {
        flex-flow: wrap;
    }
    .features--column {
        font-size: 1.5rem;
        border-bottom: 1px solid rgba(38, 173, 240, 0.705);
        margin-bottom: 2rem;
    }
    .features--column--title {
        font-size: 2rem;
    }
}

@media (max-width:1100px) {
    .search-text {
        font-size: 2rem;
        letter-spacing: 0;
    }
    .compare-text {
        font-size: 1rem;
    }
    .features--title {
        font-size: 1.5rem;
    }
    .features--column--title {
        font-size: 1.5rem;
    }
}
