*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    font-family: sans-serif;
    font-size: 16px;
    user-select: none;
}

.btn:link,
.btn:visited {
    /* padding: 1.5rem 2rem; */
    transition: all .3s;
    color: #ffffff;
    padding: 10px 20px 10px 20px;
    margin-left: 10px;
    text-decoration: none;
    display: inline-block;
    transition: .3s;
}

.btn:hover,
.btn:active {
    border-radius: .5rem;
    background-color: rgba(38, 173, 240, 0.8);
    transform: scale(1.1);
    letter-spacing: 3px;
}

.main-header {
    padding: 2rem;
    background: linear-gradient(to right bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)) , url(../images/hero.jpg) no-repeat;
    background-position: top;
    background-size: cover;
    width: 100%;
}

.header-light {
    padding: 2rem;
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.2);
}
.header-light .btn {
    color: #4f4f4f;
}

.header-light .btn:hover {
    color: #ffffff;
}

.navigation-logo {
    position: absolute;
    width: 200px;
}

.navigation-bar {
    font-size: 1.6rem;
    margin-right: 1.6rem;
    list-style-type: none;
    display: flex;
    justify-content: flex-end;
}

/* .logo {
    width: 35rem;
    margin: 1rem auto;
    height: auto;
    border: 1px solid red;
} */

.logo-image {
    width: 100%;
}

.logo-slogan {
    font-size: 2.4rem;
    text-align: center;
    color: #fff;
    font-weight: 700;
}

.search-form {
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
}

input[type=text] {
    width: 30rem;
    height: 3rem;
    margin-right: 1.5rem;
    border-radius: 1rem;
    padding: 2rem;
    border: none;
    outline: none;
}

input[type=text]::placeholder {
    transition: all .3s;
}

input[type=text]:hover::placeholder {
    letter-spacing: .1rem;
}

.btn-search {
    padding: 1.2rem;
    background-color: #26ADF0;
    color: #fff;
    font-size: 1.6rem;
    border-radius: 1rem;
    border: none;
    cursor: pointer;
    box-shadow: 2px 0 10px 2px rgba(255, 255, 255, 0.2);
    transition: all .2s;
    outline: none;
}

.btn-search:hover {
    letter-spacing: 1px;
}


.search-text {
    text-align: center;
    font-size: 2.5rem;
    margin: 3rem 0 1rem 0;
    color: rgba(255, 255, 255, .9);
    letter-spacing: .5rem;
    transition: all .3s;
}


.checkboxes {
    background-color: #fff;
    width: 35rem;
    margin: 0 auto;
    font-size: 1.6rem;
    margin-top: 2rem;
    padding: 1rem;
    border-radius: 1rem;
    display: flex;
    justify-content: center;
}

.checkbox {
    padding: 5px 1rem;
}

.checkbox:not(:last-child) {
    border-right: 1px solid grey;
}

input[type=checkbox] {
    margin-right: 1rem;
}

.compare-text {
    font-size: 1.8rem;
    margin-top: 3rem;
    text-align: center;
    color: #fff;
}

.features {
    background-color: #fff;
    padding: 3rem;
    border-radius: 30px 30px 0 0;
    box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.2);
    margin-top: 10px;
}

.features--title {
    text-align: center;
    text-transform: uppercase;
    font-size: 3.2rem;
    padding: 3.6rem;
    border-bottom: 1px solid black;
}

.features--main {
    padding: 2.4rem;
    display: flex;
}

.features--column {
    flex-grow: 1;
    font-size: 1.6rem;
    margin: 0 1rem 0 1rem;
}

.features--column--title {
    font-size: 2.4rem;
    color: #26ADF0;
}

.features--list {
    margin-top: 2rem;
    list-style-type: none;
}

.features--list li {
    padding: 1rem 0;
}

.footer {
    background-color: black;
    padding: 1.6rem;
    font-size: 1.6rem;
    color: #fff;
    text-align: center;
}

.footer p {
    padding-bottom: 1rem;
}

#bgvid {
    position: fixed;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -100;
    transform: translate(-50%, -50%);
}








.searchResult {
    min-height: 100vh;
    display: flex;
    justify-content: space-evenly;
    margin-top: 10px;
    padding: 2rem 2rem 0 1rem;
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.2);
    border-radius: 2rem 2rem 0 0;

}

.searchFilterWrapper {
    flex: 1;
    padding: 5px;
    max-height: 90vh;
    overflow: auto;
}

.searchListWrapper {
    flex: 4;
    display: flex;
    justify-content: center;
}

.SearchResultList {
    margin-left: 2rem;
}

.card {
    /* box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.2); */
    padding: 2rem;
    margin: 2rem;
}

.searchResultCard {
    margin-bottom: 5rem;
    padding: 2rem;
    width: 60rem;
    display: flex;
    transform: translateX(-10rem);
}
.cardPhoto {
    width: 10rem;
    height: 10rem;
    margin-right: 1rem;
    margin-bottom: 2rem;
}
.cardPhoto img {
    width: 100%;
    height: 100%;
    border-radius: 1rem;
}

.cardNameWrapper {
    display: flex;
    width: 100%;
    align-items: center;
}
.cardNameWrapper h1 {
    font-family: 'lato';
    font-weight: bold;
    font-size: 2rem;
}
.cardNameWrapper p {
    font-style: italic;
    display: inline-block;
    margin-left: 2rem;
}

.cardContactWrapper {
    display: flex;
}
.cardContactWrapper p {
    font-family: 'lato';
    font-size: 1.5rem;
    margin: 1rem;
    padding-right: 1rem;
}
.cardContactWrapper p:not(:nth-child(3)) {
    border-right: 1px solid #4f4f4f70;
}